.vmc-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;

    &.transparent {
        background-color: transparent;
    }

    &.black {
        background-color: @MaskBackColor;
    }
}


