.vmc-box {
    height: 0;
    position: relative;

    .vmc-box-content {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-size: 100% 100%;

        img {
            width: 100%;
            height: 100%;
        }
    }
}


